.list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}
@font-face {
  font-family: "PT Sans";
  font-weight: normal;
  font-style: normal;
  font-display: swap;
  src: local("PT Sans"), url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
}
@font-face {
  font-family: "PT Sans";
  font-weight: bold;
  font-style: normal;
  font-display: swap;
  src: local("PT Sans Bold"), url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
}
body {
  min-width: 320px;
  margin: 0;
  box-sizing: border-box;
  overflow-x: hidden;
  font-size: 18px;
  line-height: 26px;
  font-family: "PT Sans", "Arial", sans-serif;
  text-transform: uppercase;
}
img {
  max-width: 100%;
  height: auto;
}
.btn {
  display: block;
  box-sizing: border-box;
  margin: 0 auto;
  padding: 16px 32px;
  border: none;
  height: 58px;
  cursor: pointer;
  background-color: #81b3d2;
  text-decoration: none;
  color: #ffffff;
  font-size: 21px;
  font-family: inherit;
}
.btn:hover,
.btn:focus {
  background-color: #669ec0;
}
.btn:active {
  background-color: #5496bd;
  color: rgba(255, 255, 255, 0.3);
}
.btn:disabled {
  background-color: #e5e5e5;
}
.picture {
  position: relative;
  overflow: hidden;
}
@media (min-width: 768px) {
  .picture--first-order {
    order: -1;
  }
}
.picture--photo {
  min-height: 400px;
}
.picture__image {
  position: absolute;
  width: 100%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  max-width: none;
  height: auto;
}
.container {
  max-width: 1200px;
  margin: 0 auto;
  background-color: #ffffff;
}
@media (min-width: 1200px) {
  .container {
    box-shadow: 0 5px 15px rgba(0, 1, 1, 0.2);
  }
}
.header {
  width: 100%;
  padding: 0;
  margin: 0;
  position: relative;
}
.header__nav {
  position: relative;
}
.header__nav--nojs .header__toggle {
  display: none;
}
@media (max-width: 767px) {
  .header__nav--close .header__item {
    display: none;
  }
}
.header__nav--close .header__toggle {
  top: 15px;
  background-image: url("../img/icon-menu.svg");
}
.header__nav--open .header__toggle::before {
  content: "";
  position: absolute;
  right: 1px;
  top: 49px;
  width: 23px;
  height: 22px;
  background: url("../img/icon-cross.svg") 50% 50% no-repeat;
  z-index: 1;
}
.header__list {
  margin: 0;
  padding: 0;
  list-style: none;
  position: relative;
}
@media (min-width: 768px) {
  .header__list {
    display: flex;
    flex-wrap: wrap;
  }
  .header__list li:nth-child(4n+2) {
    margin-right: 20%;
  }
}
@media (min-width: 768px) {
  .header__item {
    width: 20%;
    box-sizing: border-box;
    padding: 14px 20px 16px;
    text-align: center;
  }
}
.header__link {
  display: block;
  font-weight: 700;
  line-height: 40px;
  text-align: center;
  text-decoration: none;
  padding: 8px 60px;
  background-color: #81b3d2;
  color: #ffffff;
  box-shadow: inset 0 -1px 0 rgba(1, 1, 1, 0.2), inset 0 1px 0 rgba(255, 255, 255, 0.2);
}
@media (min-width: 768px) {
  .header__link {
    display: table;
    margin: 0 auto;
    padding: 0;
    line-height: 26px;
    color: #000000;
    font-size: 14px;
    box-shadow: none;
    background: none;
  }
}
.header__link:hover,
.header__link:focus {
  background-color: #669ec0;
}
@media (min-width: 768px) {
  .header__link:hover,
  .header__link:focus {
    background: none;
    color: #81b3d2;
  }
}
.header__link:active {
  background-color: #5496bd;
}
@media (min-width: 768px) {
  .header__link:active {
    background: none;
  }
}
@media (min-width: 768px) {
  .header__link--active {
    color: #766357;
  }
  .header__link--active:hover,
  .header__link--active:focus {
    color: #604e43;
  }
}
.header__link--first {
  box-shadow: inset 0 -1px 0 rgba(1, 1, 1, 0.2);
}
@media (min-width: 768px) {
  .header__link--first {
    box-shadow: none;
  }
}
