    top: 0;
    padding: 0 27px 0;
  }
}
.header__toggle {
  position: absolute;
  display: block;
  border: none;
  cursor: pointer;
  width: 24px;
  height: 22px;
  right: 19px;
  top: -32px;
  background-color: #ffffff;
}
.header__toggle::before {
  content: "";
  position: absolute;
  right: 1px;
  top: -50px;
  width: 23px;
  height: 22px;
  background: url("../img/icon-cross.svg") 50% 50% no-repeat;
  z-index: 1;
}
