  text-transform: none;
  text-align: center;
  padding-left: 77px;
}
.check__label::after {
  content: "";
  position: absolute;
  width: 56px;
  height: 56px;
  top: -15px;
  left: 0;
  border-radius: 50%;
  background-color: #f2f2f2;
}
.check__label--radio::before {
  position: absolute;
  width: 22px;
  height: 22px;
  top: 2px;
  left: 17px;
  background-color: #000000;
  z-index: 1;
  border-radius: 50%;
}
