.products-offers {
  position: relative;
  text-align: center;

  @media @tablet {
    padding-top: 28px;
  }

  &::after {
    content: "";
    position: absolute;
    top: 70%;
    left: 46%;
    width: 27px;
    height: 27px;
    background-image: url("../img/icon-map-marker.svg");

    @media @tablet {
      top: 72%;
      left: 48%;
    }

    @media @desktop {
      left: 49%;
    }
  }
}

.products-offers__wrapper {
  padding: 28px 20px 49px;
  margin: 0 auto;

  @media @tablet {
    max-width: 460px;
    padding-bottom: 58px;
  }

  @media @desktop {
    padding-bottom: 66px;
  }
}

.products-offers__title {
  font-size: 21px;

  @media @tablet {
    font-size: 30px;
  }
}

.products-offers__text {
  margin: 0;
  font-size: 14px;
  line-height: 24px;
  padding-top: 18px;
  padding-bottom: 35px;

  @media @tablet {
    padding-top: 24px;
    padding-bottom: 43px;
  }

  @media @desktop {
    padding-bottom: 48px;
  }
}

.products-offers__image {
  position: relative;
}

.products-offers__map {
  display: block;
  min-height: 350px;
}
